.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
}

.modalContent {
  text-align: center;
  width: 500px;
  height: 300px;
  padding: 25px;
  background-color: white;
  border-radius: 16px;
  min-width: 250px;
}

.text {
  margin: 20px;
}

.modal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}